import React, {useState} from 'react';
import { useHistory } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import FormHelperText from '@material-ui/core/FormHelperText';

// Components
import Footer from '../../layouts/Footer';

// Constant && Services
import AuthService from '../../services/auth.service';

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  formMessage: {
      textAlign: 'center',
      fontSize: '1.1em'
  }
}));

export default function LogIn(props) {
  const history = useHistory();
  if (AuthService.getCurrentUser()){
      history.push('/dashboard');
  }
  const classes = useStyles();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMsg, setErrMsg] = useState('');

  function handleLogIn(event){
    event.preventDefault()
    if (!username || !password){
        return;
    }
    props.setIsLoading(true);
    const fetch = AuthService.logIn(username, password).then(result => {
        if (result.isSuccess){
            history.push('/dashboard');
        } else {
          setPassword("");
            // Error message
            setErrMsg(result.message);
        }
        props.setIsLoading(false);
    }, (error) => {
      if (error) {
        props.setIsLoading(false);
      }
    });
  }

  function handleUsernameChange(evt){
    setUsername(evt.target.value);
  }

  function handlePasswordChange(evt){
    setPassword(evt.target.value);
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={(evt) => handleLogIn(evt)}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            autoComplete="username"
            autoFocus
            value={username}
            error={username === ""}
            helperText={username === "" ? 'Enter Username' : ' '}
            onChange={(evt) => handleUsernameChange(evt)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            value={password}
            error={password === ""}
            helperText={password === "" ? 'Enter password' : ' '}
            onChange={(evt) => handlePasswordChange(evt)}
          />
          <FormHelperText className={classes.formMessage} error={true}>
              {errorMsg}
          </FormHelperText>
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/signUp" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
    </Container>
  );
}